.checked-box {
    width: 100%;
    overflow: hidden;
    background: #eee;

    .top {
        width: 100%;
        height: 50px;
        background: #F7F7F7;
        line-height: 50px;
        padding-left: 20px;
        font-weight: 600;
        border-bottom: 1.5px solid #dbdee2;
        font-size: 20px;
    }
    
    >.content {
        width: calc(100% - 200px);
        overflow: hidden;
        >.con {
            width: 100%;
            height: 150px;
            border: 1.5px solid #dbdee2;

            .con-left {
                width: 30%;
                height: 100%;
                float: left;
                line-height: 150px;
                text-align: right;
                font-size: 18px;

                b {
                    color: red;
                    padding-right: 10px;
                }
            }

            .con-right {
                width: 70%;
                line-height: 150px;
                height: 100%;
                background: #F7F7F7;
                float: left;
                border-left: 1px solid #dbdee2;

                .ant-input {
                    width: 80%;
                    margin: 18px;
                }

                .avatar-uploader {
                    width: 100%;
                    height: 100%;

                    >.ant-upload {
                        width: 128px;
                        height: 128px;
                        margin: 10px;
                        background: #ededed;
                    }
                }
            }
        }

        >.last {
            width: 100%;
            height: 60px;
            border-bottom: 1.5px solid #dbdee2;

            .con-left {
                width: 30%;
                height: 100%;
                float: left;
                line-height: 60px;
                text-align: right;
                font-size: 18px;

                b {
                    color: red;
                    padding-right: 10px;
                }
            }

            .con-right {
                width: 70%;
                height: 100%;
                background: #F7F7F7;
                float: left;
                line-height: 60px;
                border-left: 1px solid #dbdee2;
            }
        }
    }

    .bottom {
        width: 100%;
        height: 150px;
        text-align: center;

        button {
            margin: 60px 30px;
            width: 80px;
            height: 36px;
            border-radius: 8px;
            outline: none;
            border: none;
            color: #fff;
            background: #cbcbcb;

            &:nth-child(1) {
                background: #3A98FA;
            }
        }
    }
}